.logo {
  background-image: url('../../images/agriculture-icon-15.jpg');
  background-repeat: no-repeat;
  background-size: 40px;
  background-position: 0.3vmin;
  
 
  
}

nav {
    position: fixed;
    z-index: 99;
    background-color: #FFF;
    z-index: 99;
    width: 100%;
    max-width: 1500px;
    margin: 0 auto;  
    padding: 12px 0;
    display: grid;
    grid-template-columns: 2fr 1fr;
    background: #FFF;
    border-bottom: 1px solid #868180 ;

   
  }
  
  
  nav ul {
    width: 100%;
    display: grid;
    list-style: none;
    align-items: center;
    padding: 0;
    margin: 0;
  } 
  
  nav ul:first-of-type{
    grid-template-columns: 1fr;

    margin-left: 10px;
  }
  nav ul:first-of-type li{
    font-size: 1.4rem;
    font-weight: bold;
    text-indent: 30px;
  }
  nav ul:first-of-type li span {
    font-weight: 500;
    color: #191970;
    font-style: italic;
    font-size: 35px;
    font-family: sans-serif;
    margin-left: 30px;

  }
  
  nav ul:last-of-type{
    grid-template-columns: repeat(3, 2fr);
  
  }
  
  
  nav ul li a{
    color:#000;
    text-decoration: none; 
    font-weight: bold;
    display: block;
    
   
  }

  @media screen and (max-width: 960px) {
    nav ul li a {
      font-weight: bold;
      font-size: 12px;
      
    }

    nav {
      position: absolute;
    }

  }