
/*Estilização da section*/
.intro {
    width: 70%;
    padding-top: 90px;
    padding-right: 0px;
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 0px;
    margin-left: auto;
    display: grid;
    grid-template-columns: 2fr 3fr;
    align-items: center;
    
}

/*Estilização do Título*/
#title {
    font-size: 50px;
    margin-right: 2px;
    color: #FFF;
    text-transform: uppercase;
    line-height: 6rem;
    padding-right: 10px;
    border-right: 2px solid #FFF;
}

/*Estilização da div sem o ID*/
div {
    position: relative;
    
}

/*Estilização do Paragrafo*/
.text {
    padding-left: 12px;
    color: #FFF;
    font-size: 20px;
}

@media (max-width: 960px) {
    .intro {
        display: grid;
        grid-template-columns: 1fr;
        width: 100%;
        
    }

    #title {
        border-right: none;
        text-align: center;
        border-bottom: 2px solid #FFF;
    }
}